import React, { useState, useEffect } from 'react';
import  memberUtils from './helpers/members'
import DataEntry from './components/DataEntry'

const App = () => {
    const [entries, setEntries] = useState([])

    useEffect(() => {
        memberUtils
            .getAll()
            .then(res => {
                setEntries(res.data)
            })
    })

    const renderEntries = () => {
        const entryComponents = entries.map(entry => <DataEntry name={entry.name} />)
        return entryComponents
    }
    return (
        <div>
            <h1>ODL DATABASE MANAGEMENT</h1>
            <button  onClick = {() => memberUtils.update()}>Update</button>
            <br />
            <h3>CURRENT ENTRIES</h3>
            {renderEntries()}
        </div>
    );
}

export default App;